.modal-select {
  position: relative;
  height: 40px;
  margin: 0 auto;
  font-family: "Jura";
  font-size: 16px;
}

.modal-select[data-state=active] .modal-select__title::before {
  transform: translate(-3px, -50%) rotate(-45deg);
}

.modal-select[data-state=active] .modal-select__title::after {
  transform: translate(3px, -50%) rotate(45deg);
}

.modal-select[data-state=active] .modal-select__content {
  width: 100%;
  opacity: 1;
}

.modal-select[data-state=active] .modal-select__label+.modal-select__input+.modal-select__label {
  padding: 5px 16px;
  width: calc(100% - 32px);
  max-height: 40px;
  border-top-width: 1px;
}

.modal-select__title {
  display: flex;
  align-items: center;
  width: calc(100% - 32px);
  padding: 5px 16px;
  border-radius: 5px;
  border: solid 1px #c7ccd1;
  cursor: pointer;
}

.modal-select__title::before,
.modal-select__title::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 16px;
  display: block;
  width: 10px;
  height: 2px;
  transition: all .3s ease-out;
  background-color: #333;
  transform: translate(-3px, -50%) rotate(45deg);
}

.modal-select__title::after {
  transform: translate(3px, -50%) rotate(-45deg);
}

.modal-select__content {
  position: absolute;
  width: calc(100% - 32px);
  top: calc(100% - 6px);
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border: 1px solid #c7ccd1;
  border-top: none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  transition: all .3s ease-out;
  opacity: 0;
  z-index: 8;
}

.modal-select__input {
  display: none;
  width: 100%;
}

.modal-select__input:disabled+label {
  opacity: .6;
  pointer-events: none;
}

.modal-select__label {
  display: flex;
  align-items: center;
  max-height: 0;
  width: 100%;
  transition: all .2s ease-out;
  cursor: pointer;
  overflow: hidden;
}

.modal-select__label+input+.modal-select__label {
  border-top: 0 solid #C7CCD160;
}

.modal-select__label:hover {
  color: #000;
}

