.bizes_bg .biz__subtitle
  height: 50px
  padding: 0px 20px
  margin-bottom: 20px

.bizes
  position: relative
  padding-top: 0
  background-color: var(--hawkes-blue)
  margin-bottom: 150px
  &__wave1,
  &__wave2
    position: absolute
    left: 0
    right: 0
    stroke: none
  &__wave1
    bottom: calc(100% - 2px)
    fill: var(--hawkes-blue)
  &__wave2
    top: calc(100% - 2px)
    fill: var(--hawkes-blue)
    z-index: -1
  &__container
    max-width: 1380px
    overflow: hidden

  &__title
    position: absolute
    top: -120px
    left: 0
    right: 0
    z-index: 2
    text-align: center
  &__text
    font-size: 20px
    text-align: center
    position: absolute
    top: -60px
    left: 0
    right: 0
    z-index: 2
    max-width: 640px
    margin: 0 auto 30px
  &__blocks
    display: flex
    flex-wrap: wrap
    justify-content: space-around
    overflow: hidden
    +sm-block
      margin: 0 0 15px
    .biz
      margin: 30px
      +sm-block
        margin: 0 0 15px
  &__blocks--blogtime,
  &__blocks--education,
  &__blocks--businessgaming
      .biz
        margin: 10px
        max-width: 310px
        +sm-block
          margin: 0 0 15px
  &__decor
    position: absolute
    top: calc(100% - 30px)
    left: -30px
    z-index: -1

.bizes-offer
  text-align: center
  &__title
    margin-bottom: 40px
  &__subtitle
    max-width: 586px
    margin: 0 auto 20px
    font-size: 18px
    line-height: 140%
    color: var(--color-midnight)
  &__flower
    position: absolute
    bottom: 0
    right: 115px

.biz
  position: relative
  display: flex
  flex-direction: column
  align-items: center
  overflow: hidden
  text-align: center
  background-color: var(--color-white)
  border-radius: 30px
  padding-bottom: 30px
  width: 100%
  max-width: 380px
  margin-left: auto
  margin-right: auto

.biz::before
  content: ''
  position: absolute
  top: 0
  bottom: 0
  right: 0
  left: 0
  background: linear-gradient(180deg, #1448B6 0%, #6893EE 100%)
  border-radius: 30px
  opacity: 0
  transition: opacity 0.3s

.biz:hover::before
  opacity: 1

.biz__link
  position: relative
  z-index: 2
  font-size: 12px
  padding: 8px 20px

.biz__btn
  display: inline-block
  text-decoration: none
  color: var(--color-dark)

.biz:hover .biz__title,
.biz:hover .biz__subtitle,
.biz:hover .biz__link
  color: var(--color-white)

.biz-img
  display: flex
  align-items: center
  z-index: 2
  height: 240px

.biz-img > img
  object-fit: cover
  width: 100%

.biz__title
  font-size: 24px
  font-weight: 600
  margin-top: 26px
  margin-bottom: 4px
  z-index: 2
  transition: color 0.3s

.biz__subtitle
  font-size: 16px
  line-height: 22px
  margin-bottom: 30px
  z-index: 2
  transition: color 0.3s

.biz__link > span
  margin-left: 4px
  font-size: 16px

@media screen and ( max-width:  1400px)
  .bizes__title
    top: -100px
  .bizes__text
    position: static

@media screen and ( max-width: 1200px )
  .bizes__title
    top: -60px
  .biz__title
    font-size: 16px

@media screen and ( max-width: 1000px )
  .bizes
    padding-top: 60px

@media screen and ( max-width: 900px )
  .bizes__title
    max-width: 500px
    margin: 0 auto

@media screen and ( max-width:  768px)
  .bizes__title
    top: -30px
  

@media screen and ( max-width: 620px )
  .bizes-offer__flower
    display: none
  .bizes__decor
    display: none

@media screen and ( max-width: 576px )
  .bizes__title
    position: static
    transform: initial
    text-align: center
    margin-bottom: 40px

@media screen and ( max-width: 450px )
  .biz__title
    font-size: 18px
    height: 36px
