@import './company-indicators'

.hero__container
  display: grid
  align-items: center
  min-height: 750px
  height: calc(100vh - 100px)
  max-width: 1190px
  padding-top: 24px
  padding-bottom: 24px
  overflow: hidden


.hero-video 
  position: fixed
  top: 0
  left: 0
  z-index: 100
  width: 100%
  height: 100%
  background-color: rgba(0, 0, 0, 0.4)
  display: flex
  justify-content: center
  align-items: center


.hero-video__body 
  position: relative
  width: 80%
  max-width: 720px
  height: 405px


.hero-video__embed 
  position: absolute
  top: 0
  left: 0
  width: 100%
  height: 100%


.hero-video__close 
  position: absolute
  z-index: 200
  right: -25px
  top: 0
  stroke: var(--color-black)
  -webkit-transition: stroke .3s
  transition: stroke .3s
  cursor: pointer


.hero-video__close:hover 
  stroke: var(--color-white)

.hero-content 
  position: relative
  display: grid
  grid-template-areas: "text img" "btns img"
  grid-template-columns: 5fr 7fr
  grid-template-rows: min-content min-content
  grid-gap: 30px
  align-items: center

.hero-text 
  grid-area: text

.hero-text__info 
  margin-bottom: 18px

.hero-img 
  position: relative
  grid-area: img

.hero-img__img 
  width: 753px
  height: 100%

.hero-btns 
  grid-area: btns
  position: relative
  display: flex
  align-self: start

.hero-btn 
  display: block
  width: 100%
  max-width: 250px
  white-space: nowrap

.hero-title 
  font-size: 72px
  font-weight: 700
  line-height: 100%
  color: var(--color-blue)
  margin-bottom: 22px
  letter-spacing: 1.2

.hero-subtitle
  padding-right: 15px
  font-size: 18px
  line-height: 29px

.hero__currency 
  position: absolute
  top: 120px
  left: 0
  right: 0
  padding: 0 10px


.hero.hero_main .hero-content 
  grid-template-columns: 5fr 7fr

.hero.hero_main .hero-img__youtube 
  position: absolute
  top: 52%
  left: 30.5%
  cursor: pointer
  display: -webkit-box
  display: flex

.hero_bt .hero-content 
  grid-column-gap: 72px
  grid-template-rows: 250px 60px

.hero_bt .hero-title__decor 
  color: var(--hawkes-blue)

+bp1050-block 

  .hero__container 
    height: initial
    align-items: end
  

  .hero-content 
    grid-row-gap: 30px
    grid-template-columns: 1fr
    grid-template-areas: "text" "img" "btns"
  

  .hero__title 
    text-align: center
    max-width: 100%
  

  .hero__subtitle 
    max-width: 700px
    margin: 0 auto
  

  .hero-text 
    justify-self: center

  .hero-img 
    justify-self: center
  
  .hero-btns 
    justify-self: center

  .hero-subtitle 
    text-align: center

  .hero.hero_main .hero-content 
    grid-template-columns: 1fr

  .hero.hero_main .hero__title 
    text-align: center
    max-width: 840px
    margin: 0 auto 30px

  .hero_bp .hero__title,
  .hero_bp .hero__subtitle 
    text-align: center
    max-width: 100%
  
  .hero_bg .hero__subtitle 
    text-align: center
    max-width: 100%
  
  .hero_bt .hero-content 
    grid-template-rows: initial
  

@media screen and ( max-width: 768px )
  .hero-title 
    font-size: 48px
    text-align: center

  .hero-subtitle 
    font-size: 18px
  

@media screen and ( max-width:  576px)
  .hero-video__body 
    height: 250px

@media screen and ( max-width: 450px )
  
  .hero__container 
    min-height: initial

  .hero-video__body 
    height: 200px

  .hero-btns 
    flex-direction: column
    justify-self: start
    width: 100%

  .hero-btn 
    width: initial
    max-width: initial

  .hero-title 
    font-size: 38px

  .hero-btn 
    font-size: 14px
  