.plans
  overflow: hidden
  &__container 
    display: grid
    grid-template-areas: "title title title" "item1 item2 item3"
    grid-template-columns: 1fr 1.5fr 1fr
    align-items: center
    overflow: hidden

  &__title 
    text-align: center
    grid-area: title
    margin-bottom: 50px
    +sm-block
      text-align: left !important
    @media screen and ( max-width: 350px )
      max-width: 190px
      font-size: 26px      
  
.plans-col
  &--1 
    grid-area: item1
    max-width: 300px
    .plans-col
      &__item-nums 
        text-align: right
        width: 100%
        max-width: 170px
      &__item-text 
        margin-right: 15px
  &--2
    position: relative
    z-index: -1
    grid-area: item2
    justify-self: center
  &--3 
    grid-area: item3
    .plans-col__item-nums 
      flex-grow: 1
      margin-right: 15px
    .plans-col__item-text 
      width: 170px

.plans-col__item 
  display: flex
  align-items: center
  justify-content: space-between

.plans-col__title 
  display: block
  min-width: 100px
  font-size: 24px
  margin-bottom: 10px

.plans-col__subtitle 
  font-size: 18px

.plans-col__num 
  color: var(--hawkes-blue)
  font-weight: 700
  font-size: 82px

.plans-img 
  position: relative
  

@media screen and ( max-width: 900px )
    .plans__container 
        grid-template-areas: "title title" "item1 item2" "item3 item2"
        grid-template-columns: minmax(300px, 1fr) 1.5fr
        grid-column-gap: 30px
    

    .plans-col--1 .plans-col__item 
        -webkit-box-orient: horizontal
        -webkit-box-direction: reverse
        flex-direction: row-reverse
    

    .plans-col--1 .plans-col__item-nums 
        -webkit-box-flex: 9999
        flex-grow: 9999
    

    .plans-img > img 
        z-index: -1
        position: absolute
        top: 50%
        left: -250px
        -webkit-transform: translateY(-50%)
        -ms-transform: translateY(-50%)
        transform: translateY(-50%)
        min-width: 500px

    .plans-col--1 
        grid-area: item1
        min-width: 100%
    
    .plans-col--1 .plans-col__item-nums 
        text-align: left
        width: initial
        max-width: initial
        flex-grow: 99999
    
    .plans-col--1 .plans-col__item-text 
        margin-right: 0
        width: 170px
  
    .plans-col--3 .plans-col__item-nums 
        margin-right: 0
    
    .plans-col--3 .plans-col__item-text 
        width: 170px
  

@media screen and ( max-width: 450px )
  .plans__container 
    grid-template-areas: "title item2" "item1 item1" "item3 item3"
    grid-template-columns: 3fr 1fr

  .plans__title 
    text-align: left
    align-self: center
    margin-bottom: 0

  .plans-img > img 
    left: -120px
    min-width: 200px

  .plans-col--1 
    padding-top: 30px
  

@media screen and ( max-width:  375px)
    .plans__container
        grid-template-columns: 8fr 1fr

    .plans-col--1 .plans-col__item-text, .plans-col--3 .plans-col__item-text
        width: 150px

    
    
        